@charset "utf-8";
/* CSS Document */
body { padding:0; margin:0;}
img{ vertical-align: middle;border: 0;}
/*天气*/
#chinaso_weather { float:left;}
.top_data { padding-left: 20px;}
.top_data span, .top_data a, .top_data i { float: left;height: 32px; line-height:32px;}
.top_data a {color: #555;margin-right: 8px;text-decoration: none;font-family:"simsun"; font-size:12px;}
.top_data em {height: 32px;line-height: 32px;float: left;display: inline-block;font-family: simsun;font-style: normal;}
.top_data em:hover{text-decoration:underline}
.top_data .air_quality em:hover{text-decoration:none}
.top_data em.you{color:#2efd6d}
.top_data em.liang{color:#f89e02}
.top_data em.qingdu{color:#ff7e00}
.top_data em.zhongdu{color:#d5001f}
.top_data em.zhongdu2{color:#d5001f}
.top_data em.yanzhong{color:#ff0012}
.top_weather { position:relative; z-index:10000;}
.weather_con{background:#fff; border:1px #e6e7e9 solid; font-family:"simsun"; font-size:12px; color:#555; left:10px; position:absolute; top:32px;width:330px;z-index:9999}
.weather_seven{ color:#777; position:absolute;right:10px; top:0; height:32px; line-height:32px; text-decoration:none;}
.weather_seven:hover { text-decoration:underline;}
.weather_title{padding:0 9px; height:32px; line-height:32px;clear:both; overflow:hidden;}
.weather_title i { color:#333;padding-right: 4px; float:left;font-style: normal;}
.weather_change,.weather_con span{color:#777;}
.weather_select{margin-bottom:10px;text-align:center; clear:both; overflow:hidden;}
.weather_select select{font-size:12px;width:84px; height:22px; line-height:22px; float:left; margin-left:6px;}
.weather_select .ok{width:40px; height:22px; overflow:hidden; font-size:12px; float:left; margin-left:9px; border:#999 1px solid; cursor:pointer;}
.weather_day { margin:0 9px 0 9px; padding: 10px 0; overflow:hidden; background:#f5f6f7; }
.weather_day .weather_day_l{float:left; width:145px;}
.weather_day .weather_day_l img { width:55px; margin-left:45px;}
.weather_day_wrap {float:right;margin:10px 0 0 0; width:165px;}
.weather_day_wrap .weather{color:#777;font-size:16px; font-family:"Microsoft YaHei"; }
.quality i{ color:#e6855c;font-style: normal;}
.quality_degree { padding-left:5px;}
.weather_day_l .feature{ color:#8ec1e1; font-size:12px; line-height:22px;  display:block; text-align:center;}
.weather_day_wrap span{color:#777;display:block;font-size:12px; line-height:22px;}
.weather_list {padding:10px 9px}
.weather_list a{color:#999;display:inline-block;line-height:20px;text-align:center;width:104px; height:125px; overflow:hidden; float:left; text-decoration:none;}
.weather_list img{margin-top:4px;width:42px}
.weather_list a:hover { background: #f5f6f7; border-radius: 1px; text-decoration:none;color:#999 !important;}
.weather_horn { width:9px; height:6px; display:block; background:url(./chinaso_index_ico.png) -49px -394px no-repeat; position:absolute; left:32px;  top:-6px; z-index:3}
.weather_city,.weather_change { float:left;color: #00af19; text-decoration:none;}
.weather_change:hover { text-decoration:underline;}
.webNew .top_weather { display:none;}
.top_data i {font-style: normal;color: #ccc;margin: 0 0 0 6px; font-size:12px;}
.top_con {font-family: simsun,"Arial Unicode MS",System;}



